<template>
  <div>
    <!-- 酒店图片 -->
    <div id='pic-box'>
      <div class="f-url-box"
           :key="item.id"
           @click.stop="edit(item)"
           v-for="item in photos">
        <img :src="item.domainName+item.picUrl"
             alt="">
        <div class="f-url-edit">{{ topPic && item.topPicNum?'首页':'' }}</div>
      </div>
      <div class="f-url-box f-url-upload">
        <van-uploader :after-read="upLoadToQiniu"
                      upload-text="点击上传"
                      class="picBox">
        </van-uploader>
      </div>
      <!-- 查看原图 -->
      <van-popup v-model='show_fangPic'
                 style="width:90%;height:40%;">
        <img :src="current_pic"
             class="pig-pic"
             alt="">
      </van-popup>
      <!-- 删除或设置首页 -->
      <van-popup v-model="show_pic"
                 position="bottom">
        <div style='color:#000;font-size:14px;line-height:36px;'>
          <div v-for="(item,i) in chooseAll"
               :key="i">
            <div :style="{'color':item.num == '0'?'#000':'red'}"
                 @click="setting(item.num)">{{ item.title }}</div>
          </div>
          <div style="height:6px;background:#CDCDD1;opacity:0.2;"></div>
          <div style="color:#7C7C7C;"
               @click="show_pic = false">取消</div>
        </div>
      </van-popup>

    </div>
  </div>
</template>
<script>
import {
    GetBusinessQiniuAccesskey, // 七牛云
    GetBusinessHotelPictureByhotel, // 酒店图片
    PostBusinessHotelPictureAdd,
    PostBusinessHotelPictureLogo,
    PostBusinessHotelPictureRemove,
    GetBusinessRoomscalePicture, //房型图片
    PostBusinessRoomscalePictureRemove,
    PostBusinessRoomscalePictureLogo,
    PostBusinessRoomscalePictureAdd
} from '@/api/api_hotel';
import * as qiniu from 'qiniu-js';
import { Toast } from 'vant';
// const _env = process.env || process.env_test || process.env_production;
export default {
    data() {
        return {
            photos: [],
            qiniuDomain: 'https://upload-z2.qiniup.com',
            // imgUrl: _env.IMG_URL,
            imgUrl: process.env.IMG_URL,
            model: {
                name: '',
                icon: ''
            },
            dataForm: {
                picUrl: null,
                roomScaleId: null,
                hotelId: this
            },
            show_pic: false,
            show_fangPic: false,
            current_pic: null,
            topPic: null,
            chooseAll: [
                { num: '0', title: '设置为首页' },
                { num: '1', title: '删除图片' }
            ]
        };
    },
    created() {
        this.getqiniu();
        this.$store.commit('setHeader', `${this.$route.query.type}` == 'hotel' ? '酒店图片' : '房型图片');
    },
    methods: {
        // 七牛云
        getqiniu() {
            GetBusinessQiniuAccesskey().done((res) => {
                this.token = res.data;
                if (this.$route.query.type == 'hotel') {
                    this.getHotelPic(this.$route.query.hotelId);
                } else {
                    this.getScalePic(this.$route.query.roomScaleId);
                }
            });
        },
        // 查询酒店所有图片
        getHotelPic(hotelId) {
            GetBusinessHotelPictureByhotel({ hotelId: hotelId }).done((res) => {
                this.photos = res.data;
                this.topPic = localStorage.getItem('topPic');
                if (this.topPic) {
                    this.photos.find((item) => item.domainName + item.picUrl == this.topPic).topPicNum = 1;
                }
            });
        },
        // 查询房型图片
        getScalePic(roomScaleId) {
            GetBusinessRoomscalePicture({ roomScaleId: roomScaleId }).done((res) => {
                this.photos = res.data;
                this.topPic = localStorage.getItem('topPic');
                if (this.topPic) {
                    this.photos.find((item) => item.domainName + item.picUrl == this.topPic).topPicNum = 1;
                }
            });
        },
        // 查看原图
        check(pic) {
            this.current_pic = pic.domainName + pic.picUrl;
            this.show_fangPic = true;
        },
        // 展开
        edit(item) {
            this.currentInfo = item;
            this.show_pic = true;
        },
        // 删除或设置首页
        setting(num) {
            if (num == '0') {
                this.setFirstPic(this.currentInfo);
            } else {
                this.removeImg(this.currentInfo);
            }
            this.show_pic = false;
        },
        // 设置首页
        setFirstPic(item) {
            if (this.$route.query.type == 'hotel') {
                PostBusinessHotelPictureLogo({ hotelPictureId: item.id }).done((res) => {
                    localStorage.setItem('topPic', item.domainName + item.picUrl);
                    this.getqiniu();
                    Toast(res.msg);
                });
            } else {
                let data = {
                    picId: item.id,
                    roomScaleId: this.$route.query.roomScaleId
                };
                PostBusinessRoomscalePictureLogo(data).done((res) => {
                    localStorage.setItem('topPic', item.domainName + item.picUrl);
                    this.getqiniu();
                    Toast(res.msg);
                });
            }
        },
        // 调用删除接口
        removeImg(item) {
            if (this.$route.query.type == 'hotel') {
                PostBusinessHotelPictureRemove({ hotelPictrueId: item.id }).done((res) => {
                    Toast(res.msg);
                    this.getHotelPic(this.$route.query.hotelId);
                });
            } else {
                PostBusinessRoomscalePictureRemove({ roomPictrueId: item.id }).done((res) => {
                    Toast(res.msg);
                    this.getScalePic(this.$route.query.roomScaleId);
                });
            }
        },
        async upLoadToQiniu(req) {
            const config = {
                headers: { 'Content-Type': 'multipart/form-data' },
                domain: ''
            };
            let fileType = '';
            if (req.file.type ==
 'image/png') {
                fileType = 'png';
            } else {
                fileType = 'jpg';
            }
            // 上传时候的Token,可前端自己生成,安全起见后端生成！
            const token = this.token;
            const formdata = new FormData();
            formdata.append('file', req.file);
            formdata.append('token', token);
            let _this = this;
            const observer = {
                next(res) {
                    // ...
                },
                error(err) {
                    // ...
                },
                complete(res) {
                    _this.dataForm.picUrl = res.key;
                    _this.upLoadPic();
                }
            };
            const observable = qiniu.upload(req.file, 'key/upload/' + new Date().getTime() + '.png', token, config);
            const subscription = observable.subscribe(observer); // 上传开始
        },
        // 上传图片
        upLoadPic() {
            if (this.$route.query.type == 'hotel') {
                this.dataForm.hotelId = this.$route.query.hotelId;
                delete this.dataForm.roomScaleId;
                PostBusinessHotelPictureAdd(this.dataForm).done((res) => {
                    Toast(res.msg);
                    this.getqiniu();
                });
            } else {
                this.dataForm.roomScaleId = this.$route.query.roomScaleId;
                delete this.dataForm.hotelId;
                PostBusinessRoomscalePictureAdd(this.dataForm).done((res) => {
                    Toast(res.msg);
                    this.getqiniu();
                });
            }
        },
        beforeUpload(file) {
            const isJPG = file.type ==
 'image/jpeg' || file.type ==
 'image/png';
            const isLt10M = file.size / 1024 / 1024 < 10;
            if (!isJPG) {
                this.$message({
                    showClose: true,
                    message: '上传图片只能是JPG/PNG 格式!',
                    type: 'error'
                });
            }
            if (!isLt10M) {
                this.$message({
                    showClose: true,
                    message: '上传头像图片大小不能超过 10MB!',
                    type: 'error'
                });
            }
            return isJPG && isLt10M;
        }
    }
};
</script>
<style scoped>
#pic-box {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.myImg {
    width: 80px;
    height: 80px;
}
.f-url-box {
    display: flex;
    width: 30%;
    height: 90px;
    position: relative;
    margin: 10px 6px;
}
#pic-box:after {
    content: '';
    width: 30%;
    height: 0;
    visibility: hidden;
}
.f-url-edit {
    position: absolute;
    top: 68px;
    color: #fff;
    width: 100%;
    background: #000;
    opacity: 0.6;
    font-size: 12px;
    line-height: 22px;
}
.f-url-upload {
    display: flex;
    justify-content: center;
    background: #000;
    opacity: 0.2;
    border-radius: 4px;
}
.f-url-box img {
    width: 100%;
}
.pig-pic {
    height: 100%;
    width: 100%;
}
/deep/ .van-uploader__upload {
    background: #000;
}
/deep/ .van-uploader__upload-text {
    color: #fff;
}
</style>



